import React from 'react';
import Cards from '../data/input';

export class TV extends React.Component {
    constructor(props) {
        super(props);
        this.state = { cards: Cards }
    }
    render() {
        return (
            <div>
                <img
                    src="https://drv.tw/~amitmishra795@gmail.com/gd/WebApplications/IMG_20180914_114242.jpg"
                    alt="Alternate Text"
                    height="400px"
                />
                <img
                    src={this.state.cards[0].image}
                    alt={this.state.cards[0].message}
                    height="400px"
                />
                <p>Message</p>
                <button>Previous</button>
                <button>Next</button>
            </div>
        );
    }
}